import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Form, FormGroup, Row } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertProps,
  AlertType,
  dispatchClose,
  EditViewProps,
  ErroList,
  ERROR,
  FileInputPreview,
  InputGroupInline,
  ProcessBar,
  progressActions,
  showToast,
  SIZE_ONE_HUNDRED_BYTES,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { RuleMessage } from '../../../../models/dtos/ger/ruleMessage';
import { ImportacaoPlanoContaCSVParam } from '../../../../models/dtos/mcg/importacaoPlanoContaCSVParam';
import { importarPlanoContaCSV } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import ResultadoIntegracao from '../../../components/resultadoIntegracao/ResultadoIntegracao';

interface ImportacaoPlanoContaCSVProcessViewProps extends EditViewProps<ImportacaoPlanoContaCSVParam> {}

const ImportacaoPlanoContaCSVProcessView: FC<ImportacaoPlanoContaCSVProcessViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const [showResultView, setShowResultView] = useState(false);

  const [listResult, setListResult] = useState<RuleMessage[]>([]);

  const dispatch = useDispatch();

  const MAX_FILES: number = 100;

  const { reset, ...formProps } = useForm<ImportacaoPlanoContaCSVParam>({ mode: 'onBlur' });

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa !== null && globalReducer.globalParameter.grupoPlanoConta !== null) {
        const { empresa, grupoPlanoConta } = globalReducer.globalParameter;
        setTimeout(() => {
          reset({
            grupoPlanoContaDestino: {
              id: grupoPlanoConta.id,
            },
          });
          formProps.setValue('idEmpresa', empresa.id);
        }, 500);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  const handleImport = (data: ImportacaoPlanoContaCSVParam) => {
    const erros = validateExtras();
    if (erros.length > 0) {
      const params: AlertProps = {
        message: <ErroList erros={erros} />,
        type: AlertType.WARNING,
      };
      alertManager.emit(ACTION_EVENTS.SHOW, params);
    } else {
      dispatch(
        progressActions.showProgress({
          animated: true,
          className: 'progress-bar-rounded progress-animated-alt',
          color: 'first',
          label: 'Aguarde processando...',
          striped: true,
          value: 100,
        })
      );
      callImport(data);
    }
  };

  function callImport(data: ImportacaoPlanoContaCSVParam): void {
    importarPlanoContaCSV(data, {
      errorFunction: mensagem => {
        dispatch(progressActions.hideProgress());
        showToast(mensagem, ERROR);
      },
      thenFunction: (result: RuleMessage[]) => {
        dispatch(progressActions.hideProgress());
        setListResult(result);
        setShowResultView(true);
      },
    });
  }

  function validateExtras(): string[] {
    const erros = [];
    if (formProps.getValues().arquivos.length === 0) {
      erros.push('Nenhum Arquivo selecionado');
    }

    return erros;
  }

  return (
    <WindowComponent
      title="Importação Plano de Contas por Planilha do Excel (Formato CSV)"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMCG(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={1427}
    >
      {showResultView ? (
        <ResultadoIntegracao
          {...props}
          errorList={listResult.filter(it => !it.success)}
          successList={listResult.filter(it => it.success)}
          hidePrintButton={true}
          tabFailure="Registros Rejeitados"
          tabSuccess="Registros Aceitos"
        />
      ) : (
        <Form>
          <InputGroupInline formProps={formProps} id="idEmpresa" hidden={true} />
          <InputGroupInline formProps={formProps} id="grupoPlanoContaDestino.id" hidden={true} />
          <FormGroup tag={Row}>
            <Col>
              <FileInputPreview
                acceptedTypes=".csv"
                formProps={formProps}
                isMultiple={true}
                limitSizeBytes={SIZE_ONE_HUNDRED_BYTES}
                propImageName="arquivos"
                maxFiles={MAX_FILES}
              />
            </Col>
          </FormGroup>
          <ProcessBar handleSubmit={formProps.handleSubmit(handleImport)} handleClose={handleCancel} />
        </Form>
      )}
    </WindowComponent>
  );
};

export default ImportacaoPlanoContaCSVProcessView;
