import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import cx from 'clsx';
import React, { FC, MouseEvent, useState } from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import { Button, Nav, NavItem, NavLink, TabContent, TabPane } from 'reactstrap';
import { ColumnsProps, DefaultSortProps, dispatchClose, OrderType } from 'summer';

export interface ResultadoIntegracaoProps {
  /** Lista de erros da integração */
  errorList?: any[];
  /** Lista de sucessos da integração */
  successList?: any[];
  /** History do navegador */
  history?: any;
  /** Relatório a ser impresso */
  printReport?: any;
  /** Oculta o botão para imprimir o relatório */
  hidePrintButton?: boolean | false;
  /** Texto da aba de sucesso */
  tabSuccess?: string;
  /** Texto da aba de falhas */
  tabFailure?: string;
}

const ResultadoIntegracao: FC<ResultadoIntegracaoProps> = props => {
  const [activeTab, setActiveTab] = useState(1);
  const columns: ColumnsProps[] = [
    {
      dataField: 'message',
      sort: true,
      text: 'Descrição',
    },
  ];

  const defaultSorted: DefaultSortProps[] = [
    {
      dataField: 'message',
      order: OrderType.ASC,
    },
  ];

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  const toggleTab = ({ target }: MouseEvent) => {
    const { tabIndex } = target as HTMLElement;
    if (activeTab !== tabIndex) {
      setActiveTab(tabIndex);
    }
  };

  return (
    <>
      <Nav tabs={true}>
        <NavItem>
          <NavLink className={cx({ active: activeTab === 1 })} tabIndex={1} onClick={toggleTab}>
            {props.tabSuccess || 'Sucessos'}
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink className={cx({ active: activeTab === 2 })} tabIndex={2} onClick={toggleTab}>
            {props.tabFailure || 'Falhas'}
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent className="mb-5" activeTab={activeTab}>
        <TabPane tabId={1} className="mb-0 p-3">
          <div className="table-area">
            <BootstrapTable
              bootstrap4={true}
              bordered={true}
              columns={columns}
              condensed={true}
              className="table-scroll"
              data={props.successList || []}
              defaultSorted={defaultSorted}
              hover={true}
              keyField="id"
              noDataIndication="Nenhum registro a ser exibido."
              responsive={true}
              striped={true}
              tabIndexCell={true}
            />
          </div>
          <div key="divFotterKey" className="text-left">
            Registros: {props.successList.length}
          </div>
        </TabPane>
        <TabPane tabId={2} className="mb-0 p-3">
          <div className="table-area">
            <BootstrapTable
              bootstrap4={true}
              bordered={true}
              columns={columns}
              condensed={true}
              className="table-scroll"
              data={props.errorList || []}
              defaultSorted={defaultSorted}
              hover={true}
              keyField="id"
              noDataIndication="Nenhum registro a ser exibido."
              responsive={true}
              striped={true}
              tabIndexCell={true}
            />
          </div>
          <div key="divFotterKey" className="text-left">
            Registros: {props.errorList.length}
          </div>
        </TabPane>
      </TabContent>

      <div className="float-right" style={{ marginTop: 10 }}>
        <Button color="danger" className="mr-2" onClick={handleCancel}>
          <FontAwesomeIcon icon="times" /> Cancelar
        </Button>
      </div>
      {props.hidePrintButton ? null : (
        <div className="float-left" style={{ marginTop: 10 }}>
          <Button className="mr-2" disabled={props.errorList.length === 0} onClick={props.printReport} color="success">
            <FontAwesomeIcon icon="paste" /> Imprimir Falhas
          </Button>
        </div>
      )}
    </>
  );
};

export default ResultadoIntegracao;
